.page__header{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: left;
    width: 100%;
    height: 120px;
    background-color: #892232;
}
.header__title{
    font-size: 40px;
    padding-left: 30px;
    font-weight: 700;
}
.header__logo{
    width: 140px;
    height: 100px;
    border-radius: 50%;
    margin-left: 30px;
}